body {
  background-color: #cfb997;
  text-align: center;
}

.flex {
  display: flex;
}
.flex-row {
  flex-direction: row;
}

.flex-col {
  flex-direction: column;
}

.justify-center {
  justify-content: center;
}

.px-4 {
  padding-inline: 4px;
}

.mt-24 {
  margin-top: 24px;
}

.mt-48 {
  margin-top: 48px;
}

.mx-4 {
  margin-inline: 4px;
}

.mx-8 {
  margin-inline: 8px;
}
.mx-12 {
  margin-inline: 12px;
}

.button {
  padding: 8px 12px;
  border-radius: 12px;
  border: 0px;
  font-size: 16px;
  background-color: #fce700;
  font-family: sans-serif;
  font-weight: 600;
  cursor: pointer;
}
.selected-button {
  background-color: #1c1c1c;
  color: white;
}

.heading {
  font-family: sans-serif;
  text-decoration: underline;
}

.input-text {
  padding: 12px 16px;
  border-radius: 8px;
  border: none;
}

.my-12 {
  margin-block: 12px;
}
